body {
  margin: 0;
  font-family: system-ui;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #E7F6FF;
  font-weight: 400;
}
*{
  font-family: 'Inter', sans-serif;
}

.wrapper{
  max-width: 1080px;
  width: 100%;
  margin: 50px auto 0;
  background: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
}

header{
  padding: 40px 45px 50px 60px;
  border: 1px solid #EAEAEA;
  .headerLeft{
    display: flex;
    align-items: center;
    img{
      margin-right: 15px;
    }
    h3{
      text-transform: uppercase;
      font-weight: 700;
      padding-bottom: 5px;
    }
  }
  .headerRight{
    li:first-child>img{
      margin-right: 10px;
    }
    li:not(:last-child){
      margin-right: 30px;
    }
  }
}






.search-block{
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  padding: 0 15px;
  input{
    border: 0;
    padding: 13px;
    font-size: 16px;
    width: 200px;
  }
}

.overlay{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}
.drawer{
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 420px;
  height: 100%;
  right: 0;
  background: #FFFFFF;
  box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
  padding: 30px;
}
.items{
  flex: 1;
  overflow: auto;
  margin-bottom: 40px;
}
.cartTotalBlock{
  ul{
    margin-bottom: 40px;
    li{
      display: flex;
      align-items: flex-end;
      margin-bottom: 20px;
      div{
        flex: 1;
        height: 1px;
        border-bottom: 1px dashed #dfdfdf ;
        margin: 0 7px;
      }
    }
  }
  .greenButton{
    position: relative;
    &:hover{
      img{
        transform: translateX(5px);
      }
    }
    img{
      position: absolute;
      right: 30px;
      top: 20px;
      transition: transform 0.15s ease-in-out;
    }
  }

}


.greenButton{
    width: 100%;
    height: 55px;
    background: #9dd558;
    border-radius: 20px;
    border: 0;
    color: #FFFFFF;
    font-weight: 500;
    transition: background 0.2s ease-in-out;
  
    &:hover{
      background: lighten(#9dd558, 5%);
    }
    &:active{
      background: darken(#9dd558, 5%);
    }
}

.cartItem{
  border: 1px solid #f3f3f3;
  border-radius: 20px;
  overflow: hidden;
  padding: 20px;
  b{
    font-size: 14px;
  }
  .cartItemImg{
    height: 70px;
    width: 70px;
    background-size: contain;
    background-position: 0 -3px;
    background-repeat: no-repeat;
    margin-right: 20px;
  }
  .removeBtn{
    opacity: 0.5;
    cursor: pointer;
    transition: opacity 0.15s ease-in-out;

    &:hover{
      opacity: 1;
    }
  }
}